    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    align-Items: center;
    -ms-flex-pack: justify;
        justify-content: space-between;
    height: 100%;
}

@media (min-width: 500px) and (min-height: 400px){
    .NavigationItems{
       -ms-flex-direction: row;
