.Toolbar{
    height: 56px;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background-color: #703B09;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: justify;
        justify-content: space-between; 
    -ms-flex-align: center; 
        align-items: center;
    padding: 0px 15px 0px 5px;
    z-index: 90;
}
.Toolbar nav {
    height: 100%;
}
.Logo{
    height: 80%;
}
.Icon{
    height: 3px;
    width: 30px;
    background-color: #fff;
    margin: 5px;
}
@media (max-width:500px){
    .DesktopOnly{
        display: none;
    }
}


