.NavigationItem {
    margin: 10px 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    display: block;
    width: 100%;
}

.NavigationItem a {
    color: #8f5c2c;
    text-decoration: none;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    display: block;
    width: 100%;
}

.NavigationItem a:hover,
.NavigationItem a:active,
.NavigationItem a.active{
    color: #40a4c8;
}


@media (min-width: 500px){
    .NavigationItem {
        margin: 0;
        display: -ms-flexbox;
        display: flex;
        width: auto;
        height: 100%;
        -ms-flex-align: center;
            align-items: center;
    }
    
    .NavigationItem a {
    
        text-decoration: none;
        padding: 16px 10px;
        height: 100%;
        border-bottom: 4px solid transparent;
    }
    
    .NavigationItem a:hover,
    .NavigationItem a:active,
    .NavigationItem a.active{
        color: #fff;
        background-color: #8f5c2c;
        border-bottom: 4px solid #40a4c8;
    }
    
}


